import { Card, CardImg, CardText, CardBody, CardTitle, CardSubtitle, CardGroup, Button, Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Table, FormGroup, Label, Input, Form } from "reactstrap";
import { useState, useEffect } from "react";
import { useNavigate } from "react-router";
import { PencilFill, TrashFill } from 'react-bootstrap-icons';
import { userapi } from "../../api/user";
import axios from "axios";
import { deleteuser } from "../../api/user";
const Tables = () => {
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [deleteIndex, setDeleteIndex] = useState(null);
  const [searchQuery, setSearchQuery] = useState("");
  const [apiData, setApiData] = useState([]);
  const [filteredData, setFilteredData] = useState([]);
  const [newUser, setNewUser] = useState({
    name: "",
    email: "",
    password: "",
    image: "",
  });
  useEffect(() => {
    async function callApi() {
      try {
        const apiResult = await userapi();
        setApiData(apiResult);
        setFilteredData(apiResult);
      } catch (error) {
        console.log(error);
      }
    }
    callApi();
  }, []);
  const navigate = useNavigate();
  const handleedit = (actionType, userId) => {
    if (actionType === "add") {
      navigate("../userform", { state: { actionType } });
    } else if (actionType === "edit") {
      navigate("../userform", { state: { actionType, userId } });
    }
  };
  const toggleDeleteModal = (index) => {
    setDeleteIndex(index);
    setShowDeleteModal(!showDeleteModal);
  };
  const handleDelete = async () => {
    try {
      const deletedUserId = apiData[deleteIndex].id;
      await deleteuser(deletedUserId);
      // const updatedApiData = [...apiData];
      // updatedApiData.splice(deleteIndex, 1);
      // setApiData(updatedApiData);
      // setFilteredData(updatedApiData);
      setApiData(apiData.filter((course) => course.id !== deletedUserId));
    } catch (error) {
      console.log(error);
    }
    setShowDeleteModal(false);
  };
  const handleSearch = (query) => {
    setSearchQuery(query);
    const filteredUsers = apiData.filter((user) =>
      user.name.toLowerCase().includes(query.toLowerCase())
    );
    setFilteredData(filteredUsers);
  };
  return (
    <>
      <h4 className="caliber-font" style={{marginTop:"60px"}}>Users</h4>
      <Row>
      
        <Col lg="12" >
     
          <button onClick={() => handleedit("add")} className="btn btn-success caliber-font" style={{ float: "right", marginTop: "0px" }}>Add new user</button>
          <Input  style={{marginLeft:"5px" , }}
                    type="text"
                    name="search"
                    id="search"
                    placeholder="Search by name"
                    value={searchQuery}
                    onChange={(e) => handleSearch(e.target.value)}
                  />
          <Card style={{ marginTop: "20px" }}>
            <CardBody className="caliber-font">
              <Table bordered hover>
                <thead>
                  <tr>
                    <th>#</th>
                    <th>UserName</th>
                    <th>Email</th>
                    <th>Manage</th>
                  </tr>
                </thead>
                <tbody>
                  {(searchQuery ? filteredData : apiData).map((blg, i) => (
                    <tr key={i}>
                      <th scope="row">{blg.id}</th>
                      <td>{blg.name}</td>
                      <td>{blg.email}</td>
                      <td>
                        <button onClick={() => handleedit("edit", blg.id)} className="btn btn-sm btn-primary me-2">
                          <PencilFill />Edit
                        </button>
                        <button className="btn btn-sm btn-danger me-2" onClick={() => toggleDeleteModal(i)}>
                          <TrashFill />Delete
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Col>
      </Row>
      <Modal isOpen={showDeleteModal} toggle={toggleDeleteModal}>
        <ModalHeader toggle={toggleDeleteModal}>Delete User</ModalHeader>
        <ModalBody>
          Are you sure you want to delete this User?
        </ModalBody>
        <ModalFooter>
          <Button color="danger" onClick={handleDelete}>
            Delete
          </Button>
          <Button color="secondary" onClick={toggleDeleteModal}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
    </>
  );
};
export default Tables;